import React from 'react';
import { useAuth } from '../context/AuthContext';
import { LogIn, LogOut, AlertCircle } from 'lucide-react';

export const AuthStatus: React.FC = () => {
  const { user, signInWithGoogle, logout, demoMode } = useAuth();

  const handleSignIn = () => {
      if (demoMode) {
          alert("Firebase Configuration Missing!\n\nTo enable Cloud Sync, you must add your Firebase API keys to the .env file.\n\nRunning in Local Mode (Browser Storage only).");
          return;
      }
      signInWithGoogle();
  };

  if (user) {
    return (
      <div className="flex items-center gap-3">
        <div className="flex items-center gap-2">
            {user.photoURL ? (
                <img src={user.photoURL} alt="Profile" className="w-6 h-6 rounded-full border border-slate-600" />
            ) : (
                <div className="w-6 h-6 rounded-full bg-slate-700 border border-slate-600 flex items-center justify-center text-xs font-bold text-slate-300">
                    {user.displayName ? user.displayName[0] : 'U'}
                </div>
            )}
            <div className="flex flex-col hidden md:flex">
                <span className="text-xs font-bold text-white leading-none">{user.displayName}</span>
                <span className="text-[9px] text-slate-500 leading-none">Cloud Sync Active</span>
            </div>
        </div>
        <button 
          onClick={() => logout()}
          className="p-2 hover:bg-slate-800 rounded-full text-slate-400 hover:text-red-400 transition-colors"
          title="Sign Out"
        >
          <LogOut className="w-4 h-4" />
        </button>
      </div>
    );
  }

  return (
    <div className="flex items-center gap-2">
        {demoMode && (
            <div className="hidden md:flex items-center text-[10px] text-yellow-500 bg-yellow-500/10 px-2 py-1 rounded border border-yellow-500/20" title="Cloud Sync Disabled">
                <AlertCircle className="w-3 h-3 mr-1" />
                Local
            </div>
        )}
        <button
        onClick={handleSignIn}
        className="flex items-center gap-2 px-3 py-1.5 bg-blue-600 hover:bg-blue-500 text-white rounded-lg transition-colors text-xs font-bold shadow-lg shadow-blue-900/20 active:scale-95"
        >
        <LogIn className="w-4 h-4" />
        <span className="inline">Sign In</span>
        </button>
    </div>
  );
};
